    padding: 0.1cm 0;
    text-align: center;
    font-size: 16px;
    position: absolute; 
    bottom: 0;
    width: 100%;
    border-top: 2px solid #a32436;
}

@media (max-width: 768px) {
    .footer {
      padding: 0.1cm 0;
      /* height: 28px; */
    }
}

