.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 2rem; /* Reduced padding */
  background-color: white;
  border-bottom: 2px solid #a32436;
  height: 80px; /* Adjusted height */
}

.brand-logo-container {
  display: flex;
  align-items: center;
}

.brand-logo {
  font-size: 27px;
  color: #a32436;
  font-weight: bold;
  margin-right: 15px; /* Space between gciConnect and logo */
}

.logo {
  height: 60px;
}

.nav-links {
  list-style: none;
  display: flex;
  align-items: center;
  margin: 0;
}

.nav-links li {
  margin: 0 15px;
}

.nav-links a {
  text-decoration: none;
  font-size: 27px; /* Consistent font size */
  color: #000;
  font-weight: bold;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #a32436;
}

/* Responsive Design */
@media (max-width: 768px) {
  .nav-links {
      display: none;
  }

  .hamburger {
      display: block;
      cursor: pointer;
  }
}